@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap');

.App {
    position: relative;
}

:root {
    font-size: 16px;
    @media screen and (max-width: 700px) {
        font-size: 12px;
    }
    @media screen and (max-width: 500px) {
        font-size: 10px;
    }
}

body {
    font-family: 'JetBrains Mono', monospace;
    padding-bottom: 60px;
    @media screen and (max-width: 700px) {
        padding-bottom: 30px;
    }
}

body * {
    font-family: inherit;
}

.title {
    max-width: 766px;
    text-align: center;
    font-size: 1.8rem;
    color: black;
    margin: 0 auto;
    line-height: 2.2rem;

    @media screen and (max-width: 400px) {
        max-width: 300px;
    }
}

header {
    padding: 60px 0;

    @media screen and (max-width: 700px) {
        padding: 20px 0;
    }
}

.container {
    max-width: 1120px;
    margin: 0 auto;
    padding: 0 20px;
    @media screen and (max-width: 400px) {
        padding: 0 10px;
    }
}

.grid-container {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 20px;

    @media screen and (max-width: 700px) {
        grid-gap: 10px;
    }
    @media screen and (max-width: 400px) {
        grid-gap: 8px;
    }
}

.card {
    height: fit-content;
    position: relative;

    @media screen and (min-width: 650px) {
        &:hover {
            .wrapper {
                opacity: 1;
            }
        }
    }

    img {
        width: 100%;
        height: fit-content !important;
        object-fit: cover;
    }
}

.wrapper {
    top: 0;
    left: 0;
    position: absolute;
    opacity: 0;
    height: 100%;
    width: 100%;
    background: rgba(255, 255, 255, 0.7);
    transition: 0.8s ease all;
}

.description {
    font-size: 1rem;
    line-height: 1.4rem;
    color: #000000;
    text-align: center;
    margin: auto 0;
    position: absolute;
    top: 50%;
    left: 0px;
    width: 100%;
    transform: translateY(-50%);
    padding: 0 20px;

    @media screen and (max-width: 650px) {
        padding: 0 10px;
    }
}

.overlay {
    top: 0;
    left: 0;
    position: fixed;
    background: #000000;
    height: 100vh;
    width: 100%;
    z-index: 100;
}

.overlay-container {
    width: 100%;
    height: 100%;
    position: relative;

    > div {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        padding: 50px 0 30px 0;

        @media screen and (max-width: 650px) {
            height: fit-content;
        }
    }

    img {
        width: 100%;
        max-height: 92%;
        object-fit: contain;
    }

    .image-title {
        background: none;
        text-align: center;
        font-size: 0.8rem;
        color: #ffffff;
        line-height: 1.2rem;
        letter-spacing: 0.05rem;
        padding: 10px;
    }
}

.close-button {
    position: absolute;
    right: 20px;
    top: 20px;
    z-index: 20;
    background: none;
    border: none;
    padding: 0;

    svg {
        height: 30px;
        width: 30px;

        path {
            fill: #ffffff;
        }
    }
}

.donate {
    z-index: 15;
    display: block;
    position: fixed;
    bottom: 15px;
    right: 15px;
    border: none;
    background: rgba(229, 107, 105, 1);
    border-radius: 25px;
    height: 50px;
    width: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    animation-name: donate;
    animation-duration: 1.2s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;

    svg {
        height: 40px;
        width: 40px;
        stroke-width: 1.2;

        path {
            stroke: white;
        }
    }
}

.funds {
    margin: 0 auto;
    max-width: 800px;
}

.funds-item-link {
    display: block;
}

.funds-item {
    margin-bottom: 20px;

    &:last-child {
        margin-bottom: 0;
    }
}

.funds-item-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 20px 40px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    @media screen and (max-width: 700px) {
        padding: 10px 20px;
    }
}

.funds-item-img {
    width: 250px;
    height: 100px;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;

    @media screen and (max-width: 700px) {
        height: 80px;
        width: 150px;
    }
    @media screen and (max-width: 500px) {
        height: 65px;
        width: 110px;
    }

    img {
        object-fit: contain;
        max-height: 100%;
        max-width: 100%;
    }
}

.funds-item-text {
    font-size: 1rem;
    line-height: 1.5rem;
    max-width: 450px;
    min-width: 180px;
    text-align: justify;
    color: black;
    text-decoration: none;
    padding-left: 20px;
}

@keyframes donate {
    0% {
        box-shadow: 0 0 0px 0px rgba(229, 107, 105, 0.5);
    }
    20% {
        box-shadow: 0 0 0px 8px rgba(229, 107, 105, 0.5);
    }
    40% {
        box-shadow: 0 0 0px 0px rgba(229, 107, 105, 0.5);
    }
    60% {
        box-shadow: 0 0 0px 8px rgba(229, 107, 105, 0.5);
    }
    80% {
        box-shadow: 0 0 0px 0px rgba(229, 107, 105, 0.5);
    }
    100% {
        box-shadow: 0 0 0px 0px rgba(229, 107, 105, 0.5);
    }
}

.languages-button {
    z-index: 15;
    display: flex;
    justify-content: space-between;
    width: 200px;
    display: block;
    position: fixed;
    bottom: 80px;
    right: 15px;
    border: none;
    background: rgba(229, 107, 105, 1);
    border-radius: 25px;
    height: 50px;
    width: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 1.4rem;
    transition: 0.1s ease-in-out height;
    overflow: hidden;

    &.donate-page {
        bottom: 15px;
    }

    > .change-language-button {
        display: block;
    }

    .languages-container {
        display: none;
    }

    &.active {
        height: 160px;

        > .change-language-button {
            display: none;
        }

        .languages-container {
            display: block;
        }
    }
}

.change-language-button {
    background: none;
    height: 50px;
    width: 50px;
    border: none;
    cursor: pointer;
    font-size: 1rem;
    letter-spacing: 0.05rem;
    text-align: center;
    padding: 0;
    color: white;
}
